.algae-ui-dialog{z-index:101;position:fixed;min-width:30em;border-radius:4px;top:25%;left:50%;transform:translateX(-50%) translateY(-50%);background:#f2f3f7;box-shadow:0 0 4px 2px rgba(0,0,0,0.75)}.algae-ui-dialog-mask{z-index:100;position:fixed;left:0;top:0;width:100%;height:100%;background:rgba(0,0,0,0.25)}.algae-ui-dialog>header{display:flex;justify-content:space-between;align-items:center;font-size:18px;font-weight:600;padding:8px 24px}.algae-ui-dialog>header>.close{height:32px;width:32px;cursor:pointer;line-height:32px;text-align:center}.algae-ui-dialog>header>.close:hover{border-radius:4px;background:rgba(153,153,153,0.5)}.algae-ui-dialog>header>.close:hover>svg{fill:#0b1df8}.algae-ui-dialog>main{padding:16px 24px;border-top:1px solid #ccc;border-bottom:1px solid #ccc}.algae-ui-dialog>footer{padding:8px 24px;display:flex;justify-content:flex-end}.algae-ui-dialog>footer>.algae-ui-button{min-width:6em}.algae-ui-dialog>footer>.algae-ui-button:first-child,.algae-ui-dialog>footer>.algae-ui-button:last-child{margin-right:0}
