.algae-ui-button{padding:4px 16px;margin:4px 8px;cursor:pointer;line-height:1.6em;transition:all 250ms;fill:currentColor;font-size:14px;font-weight:400;border-radius:4px;border-width:1px;border-style:solid}.algae-ui-button:hover{fill:currentColor}.algae-ui-button:focus{outline:none}.algae-ui-button.algae-ui-button-disabled{cursor:not-allowed !important;color:rgba(0,0,0,0.3) !important;background:#f2f3f7 !important;border-color:#d9d9d9 !important}.algae-ui-button.algae-ui-button-full{margin-left:0 !important;margin-right:0 !important}.algae-ui-button.algae-ui-button-ghost{background:transparent !important}.algae-ui-button>.algae-ui-icon{height:1.2em;width:1.2em;vertical-align:-0.2em}.algae-ui-button>.algae-ui-icon.left{margin-right:4px}.algae-ui-button>.algae-ui-icon.right{margin-left:4px}.algae-ui-button>.algae-ui-icon.animation-loading{margin-right:8px;animation:spin 1.5s infinite}.algae-ui-button.algae-ui-button-default{background:#ffffff;color:black;border-color:rgba(153,153,153,0.5)}.algae-ui-button.algae-ui-button-default:hover{color:#506dfe;border-color:#506dfe}.algae-ui-button.algae-ui-button-primary{background:#506dfe;color:#ffffff;border-color:#506dfe}.algae-ui-button.algae-ui-button-primary.algae-ui-button-ghost{color:#506dfe}.algae-ui-button.algae-ui-button-primary:hover{color:rgba(255,255,255,0.85);background:rgba(80,109,254,0.75);border-color:rgba(80,109,254,0.75)}.algae-ui-button.algae-ui-button-primary:hover.algae-ui-button-ghost{color:rgba(80,109,254,0.75)}.algae-ui-button.algae-ui-button-success{background:#f2f3f7;color:#20baa4;border-color:rgba(153,153,153,0.5)}.algae-ui-button.algae-ui-button-success.algae-ui-button-ghost{border-color:#20baa4}.algae-ui-button.algae-ui-button-success:hover{background:#20baa4;color:#ffffff;border-color:#20baa4}.algae-ui-button.algae-ui-button-success:hover.algae-ui-button-ghost{color:rgba(32,186,164,0.85);border-color:rgba(32,186,164,0.75)}.algae-ui-button.algae-ui-button-danger{background:#f2f3f7;color:#f53f4a;border-color:rgba(153,153,153,0.5)}.algae-ui-button.algae-ui-button-danger.algae-ui-button-ghost{border-color:#f53f4a}.algae-ui-button.algae-ui-button-danger:hover{background:#f53f4a;color:#ffffff;border-color:#f53f4a}.algae-ui-button.algae-ui-button-danger:hover.algae-ui-button-ghost{color:rgba(245,63,74,0.85);border-color:rgba(245,63,74,0.75)}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}
