.algae-ui-progress-circle{display:inline-block;position:relative;line-height:0}.algae-ui-progress-circle circle{-webkit-transition:stroke-dasharray 0.3s;transition:stroke-dasharray 0.3s}.algae-ui-progress-circle>.algae-ui-progress-content{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-size:16px}.algae-ui-progress-circle>.algae-ui-progress-text{color:rgba(0,0,0,0.7);line-height:18px;white-space:nowrap}
