.code-demo{border:1px solid rgba(204,204,204,0.75);border-radius:4px;margin-bottom:16px;box-shadow:0 1px 4px 1px #edf4fd}.code-demo>.component-list{padding:24px 16px}.code-demo>.title{font-size:14px;font-weight:500;display:flex;align-items:center}.code-demo>.title:before{content:'';display:block;width:14px;height:1px;margin-right:4px;background:#ccc}.code-demo>.title:after{content:'';display:block;flex-grow:1;height:1px;margin-left:10px;background:#ccc}.code-demo>.content{padding:8px 16px}.code-demo>.content>span{font-size:14px;color:rgba(0,0,0,0.7)}.code-demo>.content>span>p{margin:8px 0;line-height:1.5}.code-demo>.content>span>p>code{margin:0 4px;padding:0 4px;background:#f2f3f7;border-radius:4px}.code-demo>.content>span>p>a{color:#506dfe;margin:0 4px;padding:4px 4px}.code-demo>.code-control{border-top:1px dashed #ccc;display:flex;justify-content:center;align-items:center}.code-demo>.code-control>div{margin:12px 8px;position:relative;cursor:pointer}.code-demo>.code-control>div:not(.check):hover:before,.code-demo>.code-control>div.check:before{content:'';display:block;position:absolute;top:-100%;width:1em;height:1em;background:#000;border-radius:1px;transform:rotate(45deg)}.code-demo>.code-control>div:not(.check):hover:after,.code-demo>.code-control>div.check:after{content:'';display:inline-block;font-size:12px;min-width:6em;text-align:center;padding:4px 1em;position:absolute;top:-40%;left:50%;transform:translate(-50%, -100%);background:#000;color:white;border-radius:4px}.code-demo>.code-control>div:hover.copy:after{content:'复制代码'}.code-demo>.code-control>div:hover.code-show:after{content:'显示代码'}.code-demo>.code-control>div:hover.code-hidden:after{content:'收起代码'}.code-demo>.code-control>div.check:after{content:'复制成功'}.code-demo>.code-control>div>svg{height:1em;width:1em;fill:#666666}.code-demo>.code{border-top:1px dashed #ccc;padding:16px;background:rgba(242,243,247,0.25);overflow-x:auto;display:block}
