.algae-ui-api{margin:16px 0;border-collapse:collapse;border-radius:4px;width:100%;box-shadow:0 1px 4px 1px #e3e6f5;background:#ffffff}.algae-ui-api td{text-align:left;min-height:3em;padding:1em 0 1em 16px}.algae-ui-api td:nth-child(1){width:15%}.algae-ui-api td:nth-child(2){width:40%}.algae-ui-api td:nth-child(3){width:30%}.algae-ui-api td:nth-child(4){width:15%}.algae-ui-api>thead>tr>td{font-size:16px;color:rgba(0,0,0,0.45);border-bottom:1px solid #ecedef}.algae-ui-api>tbody>tr:nth-child(2n+1){background:#f9fafc}.algae-ui-api>tbody>tr>td{font-size:14px;color:#8e9aad}.algae-ui-api>tbody>tr>td:nth-child(1){font-weight:500;color:rgba(0,0,0,0.7)}.algae-ui-api>tbody>tr>td:nth-child(3){color:#506dfe}
