.icon-display>.icons{margin:8px 0;border-top-style:dashed;border-top-width:1px;border-top-color:#ccc}.icon-display>.icons>ul{list-style:none;display:flex;flex-wrap:wrap}.icon-display>.icons>ul>li>div{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100px;width:167px;cursor:pointer;position:relative}.icon-display>.icons>ul>li>div:hover{background:rgba(80,109,254,0.75);border-radius:8px;color:white}.icon-display>.icons>ul>li>div:hover>.algae-ui-icon{transform:scale(1.2);fill:white}.icon-display>.icons>ul>li>div.active::after{content:'copyed';display:block;position:absolute;color:white;animation:copy-visible 0.5s linear forwards}.icon-display>.icons>ul>li>div>.algae-ui-icon{width:2em;height:2em;fill:black;transition:all 0.3s}.icon-display>.icons>ul>li>div>span{font-size:12px;margin-top:8px;font-family:'Lucida Console', Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace}@keyframes copy-visible{0%{transform:translateY(100%)}100%{transform:translateY(0)}}
