.algae-ui-input-wrapper{position:relative;display:inline-flex;height:36px}.algae-ui-input-wrapper>.algae-ui-input{width:100%;outline:none;padding:8px 12px;font-size:16px;border-radius:4px;border:1px solid #ccc;box-shadow:0 0 0 2px rgba(80,109,254,0);transition:all 0.5s}.algae-ui-input-wrapper>.algae-ui-input:hover{border:1px solid rgba(80,109,254,0.75)}.algae-ui-input-wrapper>.algae-ui-input:focus{border:1px solid rgba(80,109,254,0.75);box-shadow:0 0 0 2px rgba(80,109,254,0.25)}.algae-ui-input-wrapper>.algae-ui-input-suffix{position:absolute;transform:translateY(-50%);top:50%;right:0;line-height:0}.algae-ui-input-wrapper>.algae-ui-input-suffix>.algae-ui-input-icon{width:1em;height:1em;fill:#999;margin:0 8px}.algae-ui-input-wrapper>.algae-ui-input-suffix>.algae-ui-input-icon:hover{cursor:pointer}.algae-ui-input-wrapper>.algae-ui-input-after{display:inline-block;height:100%}
