.algae-ui-progress-normal{display:flex;justify-content:center;align-items:center;padding:8px 8px}.algae-ui-progress-normal>.algae-ui-progress-rail{flex-grow:1;height:8px;border-radius:4px;overflow:hidden}.algae-ui-progress-normal>.algae-ui-progress-rail>.algae-ui-progress{height:100%;transition:all 0.3s cubic-bezier(0.08, 0.82, 0.17, 1) 0s}.algae-ui-progress-normal>.algae-ui-progress-content{margin-left:8px}.algae-ui-progress-normal>.algae-ui-progress-text{color:rgba(0,0,0,0.7);line-height:18px;white-space:nowrap}.algae-ui-progress-normal>.algae-ui-progress-icon{line-height:0;border-radius:50%;overflow:hidden;fill:#ffffff;font-size:12px}
