.algae-ui-scroll-container{overflow:hidden;position:relative}.algae-ui-scroll-container>.algae-ui-scroll{overflow:auto;position:absolute;top:0;bottom:0;left:0}.algae-ui-scroll-container>.algae-ui-scroll-track{background:#ffffff;position:absolute;right:0;height:100%;opacity:0;transition:opacity 0.5s cubic-bezier(0.55, 0.055, 0.675, 0.19)}.algae-ui-scroll-container>.algae-ui-scroll-track>.algae-ui-scroll-bar{position:absolute}.algae-ui-scroll-container:hover .algae-ui-scroll-track{zoom:1;filter:alpha(opacity=50);opacity:1}
